function TabelaIndicadores() {
  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 14,
    },
  }))(TableCell);

  const StyledTableRow = withStyles((theme) => ({
    root: {
      "&:nth-of-type(odd)": {
        backgroundColor: theme.palette.action.hover,
      },
    },
  }))(TableRow);

  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="Bandeiras">
        <TableHead>
          <TableRow>
            {data.criterios.tableTitles.map((title) => (
              <StyledTableCell align="center"> {title} </StyledTableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {data.criterios.tableData.map((row) => (
            <StyledTableRow key={row.indicadores}>
              <StyledTableCell key=""> {row.indicadores} </StyledTableCell>
              <StyledTableCell align="center"> {row.vermelha[0] } &nbsp;  { row.vermelha[1] } </StyledTableCell>
              <StyledTableCell align="center"> {row.laranja[0] } &nbsp; { row.laranja[1]} </StyledTableCell>
              <StyledTableCell align="center"> {row.amarela[0] } &nbsp; { row.amarela[1]} </StyledTableCell>
              <StyledTableCell align="center"> {row.verde[0] } &nbsp; { row.verde[1] } </StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

